
footer{
    // position: absolute;
    // top: 500px;
    // padding-top: 100px;
    bottom: 0;
    left: 0;
    right: 0;
    background: #111;
    height: auto;
    // width: 100vw;
    font-family: "Open Sans";
    padding-top: 20px;
    color: #fff;
}
.footer-content{
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.footer-content h3{
    color: var(--color-secondary);
    font-size: 2rem;
    font-weight: 400;
    text-transform: capitalize;
    // line-height: 3rem;
}
.footer-content p{
    max-width: 500px;
    padding: 10px auto 10px;
    line-height: 28px;
    font-size: 16px;
    
}
.footer-socials{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 2rem 0;
    padding: 0;
}
.footer-socials li{
    margin: 0 10px;
}
.footer-socials a{
    text-decoration: none;
    color: #fff;
}
.footer-socials a i{
    font-size: 2rem;
    transition: color .2s ease;

}
.footer-socials a:hover i{
    color: var(--color-secondary);
}

.footer-bottom{
    background: #000;
    // width: 100vw;
    padding: 10px 0;
    text-align: center;
}
.footer-bottom p{
    font-size: 16px;
    word-spacing: 2px;
    text-transform: capitalize;
}
.footer-bottom span{
    // text-transform: uppercase;
    color: var(--color-secondary);
    font-weight: 400;
}

