.nav-item{
    cursor: pointer;
    font-family: "Open Sans";
}

.nav-link:hover{
    color: rgb(132, 194, 255) !important;
}

.navbar-brand{
    // cursor: pointer;
    margin-right: 0;
    font-family: "Open Sans";
    color: rgb(132, 194, 255) !important;
}

.navbar-brand:hover{
    color: white !important;
}

.navbar{
    background: rgba(17,17,17,0.8);
    // display: flex;
    z-index: 100;
}

.container-fluid{
    display: flex !important;
}

.nav-header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav-socials{
    padding-left: 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    // margin: 1rem 0 2rem 0;
    margin: auto;
}
.nav-socials li{
    margin: 0 10px;
}
.nav-socials a{
    text-decoration: none;
    color: #fff;
}
.nav-socials a i{
    font-size: 1.2rem;
    transition: color .2s ease;

}
.nav-socials a:hover i{
    color: var(--color-secondary);
}

.navbar-nav{
    position: relative;
}

.nav-item{
    
    font-size: 1.2rem;
}