.mainqcontainer{
    background-color: black;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;

}

.quotecontainer{
    position: absolute;
    /* flex-basis: 100%; */
    /* box-sizing: border-box; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* left: 10vmax; */
}

.quoteluffy{
    color: rgb(0, 0, 0);
    font-weight: 690;
    // color: rgb(0, 0, 0);
    text-align: center;
    z-index: 103;
    
    @media all and (max-width: 768px) {
        color: rgb(132, 194, 255);
      }
}

.quotehere{
    color: rgb(132, 194, 255);
    // color: rgb(0, 0, 0);
    // text-align: left;
    display: flex;
    flex-direction: row;
    align-self: start;
    z-index: 103;

    svg{
        margin-top: 15px;
        margin-left: 5px;
    }

    @media all and (max-width: 768px) {
        display: none;
      }
}

